    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.game-body{
    padding: 3rem 1.6rem;
    border-radius: .6rem;
    box-shadow: 2px 2px 12px rgba(0, 0, 0, 1);
    height: 75vh; 
    display: flex;
    gap: 1rem;
    flex-direction: column;
    justify-content: space-around;
    width: clamp(280px, 70%, 590px);
}

.instructions{
    background-color: #e01492;
    color: #f5f5f5;
    padding: .8rem;
    font-size: .9rem;
}

button{
    background-color: #ffffff;
    color: #333333;
    padding: .45rem 1.3rem;
    border-radius: .4rem;
    margin: 1rem;
    border: none;
    cursor: pointer;
}

button:hover{
    color: #f5f5f5;
    background-color: #e01492;
}

input{
    width: 60%;
    padding: .3rem 1rem;
    border-radius: .1rem;
    outline: 0;
    border: 0;
}

input:focus{
    outline: 3px solid #2314f5
}

.result{
    height: 10rem;
    width: 70%;
    padding: 1rem;
    background: #f5f5f5;
    border-radius: .2rem;
    color:  #333333;
}